.box-column {
    -ms-flex-positive: 1;
        flex-grow: 1;
    -ms-flex-preferred-size: 0;
        flex-basis: 0;
    color: black;
    display: block;
    padding: 2.5rem;
    border-right: 1px solid #e0e0e0;
    border-bottom: 0px solid #e0e0e0;
}

@media only screen and (max-width: 750px) {
    .box-column {
        border-right: 0px solid #e0e0e0;
        border-bottom: 1px solid #e0e0e0;
    }
}


