.section {
    width: 100%;
}

.section-content {
    padding: 2.5rem 2.5rem;
    margin: 0 auto;
    max-width: 65rem;
}

.section-content p {
    word-wrap: break-word;
}

.intro {
    padding-top: 2.5rem;
    padding-bottom: 10rem;
    color: white;
    text-align: center;
}

.intro p {
    font-size: 1.5rem;
}

.intro h1 {
    font-size: 2.25rem !important;
}

.intro a {
    width: 50%;
    margin: 0 auto;
}

.resume {
    font-size: 1rem;
}

.terminal {
    min-height: 100vh;
    margin-top: 15vh;
}

.terminal a, a:visited, a:hover {
    color: black;
    font-size: 1.5rem;
    text-decoration: underline;
    cursor: pointer;
}


