.no-border {
  border: none !important;
}

.footer {
  color: white;
  text-align: center;
  padding-top: 7.5rem;
}

.footer a i {
  height: 1.75rem;
  width: 2.5rem;
  border-radius: 0.25rem;
  border: 2px solid #ff867f;
  color: #fff;
  padding-top: 0.25rem;
  font-size: 1.5rem;
  text-align: center;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  margin: 5px;
}

.footer a i:hover {
  color: #ff5252;
  background-color: white;
  border-color: white;
}


